import React, { useState } from "react";
import NavBar from '../components/NavBar'

export default function Layout(props){

    return <>

    
    <body className={props.page}>
        <NavBar/>

        <div className="container ContentArea contentAreaDark">
            <div className="headingImg">
                <img src="/images/1927860_6084995367_1535_n.jpg" alt="headingImg" />

            </div>



        </div>

        






        {props.children}


    </body>



    


    </>
}